<template>

    <!-- 简短的介绍内容 -->

    <div id="BriefIntroduction" @click="AccessDetails(Brief.id)" >

        <van-image :src="Brief.img" lazy-load />

        <div class="text">
            <div class="top">
                <span class="name">{{Brief.title}}</span>
                <span class="score">
                    {{Brief.rating}}<i>分</i>
                </span>
            </div>
            <div class="bottom">
                <p>
                    {{Brief.longIntro}}
                </p>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    props: {
        Brief:{
            type:Object
        }
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style lang="less">
    #BriefIntroduction {
        display: flex;
        margin-bottom: 20px;
        .van-image {
            width: 25%;
            box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
        }
        .text {
            width: 70%;
            margin-left: 10px;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            padding: 10px;
            .top {
                flex: 1;
                display: flex;
                justify-content: space-between;
                align-items:center;
                .name {
                    width: 80%;
                    font-family: "黑体";
                    font-size: 18px;
                    color: #222222;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .score {
                    line-height: 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: #fca000;
                    i {
                        font-size: 13px;
                        font-weight: 500;
                        font-style: normal;
                    }
                }
            }
            .bottom {
                display: flex;
                align-items:center;
                p {
                    font-size: 14px;
                    line-height: 24px;
                    color: #999999;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
    }
</style>