<template>

    <!-- 高分神作与独家原创组件 -->

    <div id="HighlyScore">
        
        <TitleComponent>
            <slot></slot>
        </TitleComponent>

        <ul>
            <AllUniversalSom
                v-for="(item, index) in items"
                :key="index"
                :item="item"
                :style="{width:num+'%'}"
            ></AllUniversalSom>
        </ul>

    </div>

</template>

<script>
import TitleComponent from '@/components/TitleComponent.vue';
import AllUniversalSom from '@/components/AllUniversalSom.vue';

export default {
    components: {
        TitleComponent,
        AllUniversalSom
    },
    props: {
        High: {
            type:Array
        },
        Exclusive: {
            type:Array
        }
    },
    data() {
        return {
            items: [],
            num: 22.4,
        }
    },
    mounted() {
        if(this.High) {
            this.items = this.High
        }
        if(this.Exclusive) {
            this.items = this.Exclusive
        }
    },
}
</script>

<style lang="less">
    #HighlyScore {
        padding: 10px;
        ul {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }
</style>