<template>

    <!-- 书城女生 -->

    <div id="Girl">

        <GeneralSwipe
            :items="items"
        ></GeneralSwipe>

        <ScripturesRecommend
            :General="General"
        ></ScripturesRecommend>

        <RankingList
            :Daily="Daily"
        >
            今日大热榜 · {{date}}
        </RankingList>

        <HighlyScore
            :High="High"
        >
            高分神作
        </HighlyScore>

        <HighlyScore
            :Exclusive="Exclusive"
        >
            独家原创
        </HighlyScore>

        <RankingList
            :Ranking="Ranking"
        >
            各类排行榜精选
        </RankingList>

        <UniversalNovelModule
            :Ancient="Ancient"
            :AncientOne="AncientOne"
        >
            古代言情
        </UniversalNovelModule>

        <UniversalNovelModule
            :Modern="Modern"
            :ModernOne="ModernOne"
        >
            现代言情
        </UniversalNovelModule>

    </div>

</template>

<script>
import GeneralSwipe from '@/components/GeneralSwipe.vue';
import ScripturesRecommend from '@/components/ScripturesRecommend.vue';
import RankingList from '@/components/RankingList.vue';
import HighlyScore from '@/components/HighlyScore.vue';
import UniversalNovelModule from '@/components/UniversalNovelModule.vue';


export default {
    components: {
        GeneralSwipe,
        ScripturesRecommend,
        RankingList,
        HighlyScore,
        UniversalNovelModule,
    },
    data() {
        return {
            date: "",
            items: [
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16062872009171063.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16062874639131325.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16062873627945760.jpg"
                }
            ],
            GeneralList: [
                "萌宝来袭爸比九块九",
                "法医丑妃之王爷晚上见",
                "贵妃你又作死了"
            ],
            General: [],
            DailyList: [
                "穿成残疾大佬的小娇妻",
                "重生七零年代好孕妻",
                "我是旺夫命",
                "八零娇娇女",
                "邪王宠上天",
                "快穿女配作妖日常"
            ],
            Daily: [],
            HighList: [
                "穿书之贵女咸鱼日常",
                "重生之再为将军妻",
                "天价宠婚凌少别太坏",
                "重生惊澜无双",
                "宫女上位守则",
                "农门长嫂当家纪事",
                "快穿之女配才是大佬",
                "重生之女王归来",
            ],
            High: [],
            ExclusiveList: [
                "邪王独宠逆天御兽师",
                "骁爷的小可爱长大了",
                "问仙路",
                "娇妃有毒",
                "傅少的隐婚甜妻",
                "重生千金她又虐渣了",
                "你是人间星火",
                "顾爷今天也在单相思"
            ],
            Exclusive: [],
            RankingList: [
                "上古",
                "且以情深换白首",
                "蜜宠娇妻",
                "萌宝驾到",
                "快穿之系统逼我做渣女",
                "法医王妃",
            ],
            Ranking: [],
            Ancient: [],
            Modern: [],
            AncientOne: {},
            ModernOne: {},
        }
    },
    activated() {

        this.$store.commit('Eliminate');

        this.$store.commit('two')
        this.date = null;
        var d = new Date();
        var n = d.getDate();
        var m = d.getMonth()
        this.date = m+1+"月"+n+"日"
    },
    created() {

        for(let i=0; i<this.GeneralList.length ; i++) {
            this.axios.get(`search?keyword=${this.GeneralList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.General.push(obj);
            })
        }

        for(let i=0; i<this.DailyList.length ; i++) {
            this.axios.get(`search?keyword=${this.DailyList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    ratio: respaonse.data.books[0].retentionRatio+"万热度",
                    id: respaonse.data.books[0]._id,
                }
                this.Daily.push(obj);
            })
        }

        for(let i=0; i<this.HighList.length ; i++) {
            this.axios.get(`search?keyword=${this.HighList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.High.push(obj);
                
            })
        }

        for(let i=0; i<this.ExclusiveList.length ; i++) {
            this.axios.get(`search?keyword=${this.ExclusiveList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.Exclusive.push(obj);
            })
        }

        for(let i=0; i<this.RankingList.length ; i++) {
            this.axios.get(`search?keyword=${this.RankingList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    ratio: respaonse.data.books[0].retentionRatio+"万热度",
                    id: respaonse.data.books[0]._id,
                }
                this.Ranking.push(obj);
            })
        }

        this.axios.get(`category-info?gender=female&major=古代言情`).then((respaonse)=>{
            this.axios.get(`book-info/${respaonse.data.books[10]._id}`).then((respaonse)=>{
                var split = respaonse.data.cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.title,
                    id: respaonse.data._id,
                    longIntro:respaonse.data.longIntro,
                    rating:respaonse.data.rating.score
                }
                this.AncientOne = obj;
            })

            for(let i=1; i<9; i++) {
                var split = respaonse.data.books[i].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[i].title,
                    id: respaonse.data.books[i]._id,
                }
                this.Ancient.push(obj);
            }
        })

        this.axios.get(`category-info?gender=female&major=现代言情`).then((respaonse)=>{

            this.axios.get(`book-info/${respaonse.data.books[10]._id}`).then((respaonse)=>{
                var split = respaonse.data.cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.title,
                    id: respaonse.data._id,
                    longIntro:respaonse.data.longIntro,
                    rating:respaonse.data.rating.score
                }
                this.ModernOne = obj;
            })

            for(let i=1; i<9; i++) {
                var split = respaonse.data.books[i].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[i].title,
                    id: respaonse.data.books[i]._id,
                }
                this.Modern.push(obj);
            }
        })

    },
}
</script>

