<template>

    <!-- 最通用的子组件 -->

    <li id="AllUniversalSom" @click="AccessDetails(item.id)" >
        <van-image :src="item.img" lazy-load />
        <p>{{item.title}}</p>
    </li>
</template>

<script>
export default {
    props: {
        item: {
            type: Object
        }
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style lang="less">
    #AllUniversalSom {
        .van-image {
            min-width: 100%;
            min-height: 80px;
            border-radius: 3px;
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
            img {
                border-radius: 3px;
            }
        }
        p {
            font-size: 15px;
            line-height: 22px;
            color: #222222;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
</style>