<template>

    <!-- 通用的男女小说组件 -->

    <div id="UniversalNovelModule">

        <TitleComponent>
            <slot></slot>
        </TitleComponent>

        <BriefIntroduction
            :Brief="Brief"
        >
        </BriefIntroduction>

        <ul>
            <AllUniversalSom
                v-for="(item, index) in items"
                :key="index"
                :item="item"
                :style="{width:num+'%'}"
            ></AllUniversalSom>
        </ul>

    </div>
</template>

<script>
import TitleComponent from '@/components/TitleComponent.vue';
import AllUniversalSom from '@/components/AllUniversalSom.vue';
import BriefIntroduction from '@/components/BriefIntroduction.vue';

export default {
    components: {
        TitleComponent,
        AllUniversalSom,
        BriefIntroduction,
    },
    props: {
        Ancient: {
            type:Array
        },
        Modern: {
            type:Array
        },
        AncientOne: {
            type:Object
        },
        ModernOne: {
            type:Object
        },
    },
    data() {
        return {
            items: [],
            Brief: {},
            num: 22.4,
        }
    },
    mounted() {
        if(this.Ancient) {
            this.items = this.Ancient
        }
        if(this.Modern) {
            this.items = this.Modern
        }
        if(this.AncientOne) {
            this.Brief = this.AncientOne
        }
        if(this.ModernOne) {
            this.Brief = this.ModernOne
        }
    },
    watch: {
        AncientOne(n) {
            this.Brief = n
        },
        ModernOne(n) {
            this.Brief = n
        }
    }
}
</script>

<style lang="less">
    #UniversalNovelModule {
        padding: 10px;
        ul {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }
    
</style>