<template>

    <!-- 通用轮播图组件 -->

    <div id="GeneralSwipe">

        <van-swipe
            class="my-swipe"
            :autoplay="5000"
            indicator-color="#ffffff"
        >
            <van-swipe-item
                v-for="(item, index) in items"
                :key="index"
                :style="{backgroundColor:color}"
            >
                <img :src="item.img">
            </van-swipe-item>
        </van-swipe>

    </div>

</template>

<script>
export default {
    props: {
        color: {
            type: String
        },
        items: {
            type: Array
        },
    },
    data() {
        return {
        }
    },
}
</script>

