    #GeneralSwipe {
        padding-top: 16px;
        background-color: #ffda33;
        .my-swipe{
            .van-swipe-item {
                padding: 0 16px;
                background-color: #ffda33;
                img {
                    border-radius: 10px 10px 0 0 ;
                    vertical-align: bottom;
                }
            }
        }
    }
</style>